___CSS_LOADER_EXPORT___.push([module.id, `body,
html {
  height: 100%;
}

#homepage-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

#navbar-container {
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1fr;
  grid-template-rows: 1;
  align-items: center;
  gap: 10%;
  --box-shadow: 0 0 25px 5px;
  box-shadow: var(--box-shadow);
  z-index: 1;
}

#homepage-body {
  flex-grow: 1;
  background-color: rgb(157, 255, 186);
  z-index: 0;
}

.nav-item {
  position: relative;
  text-align: center;
  padding: 20px;
}

.menu-container {
  position: absolute;
  left: 0;
  top: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.menu-item {
  left: 0;
  width: 100%;
  text-align: center;
  display: none;
  padding: 10px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  z-index: 1; /* Ensures submenu appears above other content */
  box-shadow: 0 5px 10px 0;
}

.hidden {
  display: none;
}

.visible {
  display: block;
  /* position: absolute; */
}
